import { Directive, ElementRef , Input   } from '@angular/core';
import { CdsInternalPointer } from '@cds/core/internal-components/popup';

@Directive({ selector: 'cds-internal-pointer' })
export class CdsInternalPointerDirective {
  protected element: CdsInternalPointer;

  get axisAlign() {
    return this.element.axisAlign;
  }
  @Input() set axisAlign(value) {
    this.element.axisAlign = value;
  };
  get type() {
    return this.element.type;
  }
  @Input() set type(value) {
    this.element.type = value;
  };


  constructor(elementRef: ElementRef) {
    this.element = elementRef.nativeElement;
  }
}
